const api = {
  baseApiUrl: 'http://localhost:8080/api/v1',
  buildUrl(apiName){
    return new URL([this.baseApiUrl, apiName].join('/'));
  },

  fetchInit: (method, data) => {
    let init = {
      method: method,
      //headers: getters.headers,
    };
    if (data)
      init['body'] = JSON.stringify(data);
    return init;
  }
}
export default api;
