import api from "@/api/common";

const apiName = 'field'
async function getAllFields(page, size) {
  const url = api.buildUrl(apiName);
  let params = {
    'page': page,
    'size': size
  };

  url.search = new URLSearchParams(params).toString();

  let resp = await fetch(url, api.fetchInit('get') );

  let res;
  let err;
  let json = await resp.json();
  if(resp.ok) {
    res = {
      fields:  json.content,
      totalPages: json.totalPages,
      totalElements: json.totalElements,
      elementsOnPage: json.numberOfElements,
    };
  } else {
    err = json.error;
  }

  return {res, err}

}

let response = getAllFields(0, 3);
console.log(response)

const {res, err} = response
if(!err){
  console.log("all is ok")
  console.log(res)

} else {
  console.log("error")
  console.log(err)
}
export default getAllFields;